import React, {Component} from "react";
import ProductListPriceFilterRowView from "./ProductListPriceFilterRowView"
import OnlineUtils from "eshop/utils/OnlineUtils";

class ProductListOneTimeMobileFilterView extends React.Component {

    render() {
        return (
            <div>
                {
                    this.props.showComponent ?
                        <div id={this.props.id}>
                            <p className="h4 u-spacing-l">{this.props.header}</p>
                            <div className="l-row u-spacing-l u-no-margin"  data-js-module="common/modules/opl-gradient-fade" data-js-options={JSON.stringify({'coverClass':'.cover','triggerHideName':'Zwiń','triggerShowName':'Rozwiń','maxHeight':300,'showFirst':this.props.showExpanderNumber,'scrollTop':false,'scrollSet':0})}>
                                <ul className="opl-gradient-fade--wrapper">
                                    {this.props.tieredPrices && this.props.tieredPrices.map((price,index) =>
                                        <ProductListPriceFilterRowView key={"Price_"+price+"_"+index}
                                            type={this.props.type}
                                            checked={this.props.selectedTieredPrices[price.id]}
                                            onClick={() => this.props.onClick(price.id)}
                                            text={this.props.labelFormatter(price)}
                                            />
                                    )}
                                </ul>
                                <div className="opl-gradient-fade--trigger u-text-left u-spacing-m u-spacing-top">
                                    <a href="#" className="opl-load-more u-font-bold">Rozwiń</a></div>
                            </div>
                        </div>
                    : null
                }
            </div>
       )
   }
}

export default ProductListOneTimeMobileFilterView;